.container {
  margin: 0 auto;
  position: relative;
  width: $page_max_width;

  @include screen-lg() {
    padding: 0 55px;
    min-width: $screen_xs;
    overflow: hidden;
    width: auto;
  }

  @include screen-sm() {
    padding: 0 8px;
  }
}

.container-sm {
  margin: 0 auto;
  position: relative;
  width: $page_sm_width;

  @include screen-lg() {
    min-width: $screen_xs;
    width: auto;
  }
}


// header
.header {
  justify-content: space-between;
  display: flex;
  padding: 30px 0 30px;
  position: relative;
  z-index: 10;
}

.nav {
  display: flex;
  margin-top: 12px;
  align-items: center;

  .icon-close {
    cursor: pointer;
    display: none;
    margin-right: 30px;
  }

}

.menu {
  display: flex;
  margin-top: 0;

  .menu-item {
    list-style: none;
    margin: 0 38px 0 0;

    a {
      display: inline-block;
      color: $font_color_base;
      font-size: $font_size_h5;
      font-weight: 600;
      text-decoration: none;
      text-transform: capitalize;
      transition: color 0.2s ease;

      &:after {
        background: $primary;
        border-radius: 5px;
        display: block;
        content: "";
        height: 5px;
        margin-top: 9px;
      }
    }

    &:hover a,
    &.active a {
      color: $primary;
    }
  }
}

.lang {
  color: $font_color_base;
  font-size: $font_size_lg;
  align-items: center;
  display: flex;
  margin-right: 13px;
  margin-bottom: 15px;
  text-decoration: none;

  .icon {
    margin-left: 5px;
  }
}

.logo {
  display: block;
  text-decoration: none;
  position: relative;
  z-index: 10;

  .logo-img {
    display: block;
  }

  .logo-text {
    color: $font_color_base;
    display: block;
    letter-spacing: 0.225em;
    margin-top: 24px;
    font-size: 13px;
    font-weight: 800;
    text-transform: uppercase;
  }

  @include screen-sm {
    .logo-text {
      margin-top: 12px;
    }
  }
}

.footer {
  background: $gray-4;
  border-radius: 60px 60px 0 0;
  display: flex;
  justify-content: space-between;
  padding: 35px 60px;
  position: relative;
  width: 100%;

  .copyright {
    font-size: 12px;
    margin-top: 10px;
  }

  .logo {
    display: inline-flex;
  }

  .logo-text {
    margin-top: 10px;
    margin-left: 25px;
  }

  @include screen-md() {
    padding: 35px 53px;

    .copyright {
      color: $primary;
      top: 60px;
      position: absolute;
      right: 35px;
    }
  }

  @include screen-sm() {
    padding: 32px 42px;
    display: block;

    .logo {
      display: block;
      margin-bottom: 60px;
    }
    .logo-text {
      margin-top: 24px;
      margin-left: 0;
    }
    .copyright {
      position: static;
      text-align: right;
    }
  }
}


.nav-mobile {
  display: none;

  .icon-menu-mobile {
    margin-right: 30px;
  }
  .lang {
    margin-bottom: 0;
  }
}

@include screen-container-sm() {
  .nav-mobile {
    display: flex;
    align-items: center;
  }

  .nav {
    display: flex;
    position: fixed;
    left: 0;
    right: 0;
    padding: 50px;
    background: #f1f0f0;
    border-radius: 0 0 30px 30px;
    margin: 0;
    justify-content: flex-end;


    transition: top 0.35s ease;
    top: -100%;

    &.slide {
      top: 0;
    }

    .icon-close {
      display: block;
    }
  }

  .lang {
    display: flex;
    justify-content: flex-end;
  }
}


@include screen-md() {
  .nav-mobile {
    .lang {
      display: none;
    }
  }
  .nav {
    display: block;

    .icon-close {
      display: block;
      position: absolute;
      right: 5px;
      top: 50px;
    }

    .menu {
      display: block;
      padding-top: 160px;
      width: 100%;

      .menu-item {
        display: block;
        margin-bottom: 50px;
        margin-right: 0;
      }

      a {
        display: block;
      }
    }
  }
}


.icon-menu-arrow {
  background: url(../img/icons/arrow-menu.png) no-repeat;
  background-size: contain;
  display: block;
  height: 12px;
  width: 12px;
}

.icon-close {
  background: url(../img/icons/close.png) no-repeat;
  background-size: contain;
  display: block;
  height: 33px;
  width: 33px;
}

.icon-menu-mobile {
  background: url(../img/icons/menu-mobile.png) no-repeat;
  background-size: contain;
  cursor: pointer;
  display: block;
  height: 31px;
  width: 41px;
}
